<template>
  <div id="add">
    <!-- 添加 -->
    <div class="wx-add">微信扫码，完成申请。</div>
    <div class="hotle-address">
      <span class="hotle-name">酒店名称：</span>
      {{ currentHotel.hotelName }}
    </div>
    <img
      style="display:inline-block;width:300px;height:300px;"
      :src="pic_code"
      alt
    />
    <div class='wx-bottom'>申请完成后，关注哎哟有房管理系统公众号</div>
  </div>
</template>
<script>
import { GetBusinessHotelOpRelationXcxCode } from '@/api/api_staff';
export default {
    data() {
        return {
            pic_code: null
        };
    },
    created() {
        this.$store.commit('setHeader', '添加角色');
        this.hotels = JSON.parse(localStorage.getItem('c_hotelAll'));
        this.currentHotel = this.hotels.find((v) => v.id == this.$route.query.hotelId);
        this.getCode();
    },
    methods: {
        // 获取酒店添加二维码
        getCode() {
            GetBusinessHotelOpRelationXcxCode({ hotelId: this.$route.query.hotelId }).done((res) => {
                this.pic_code = 'data:image/png;base64,' + res.data;
            });
        }
    }
};
</script>
<style scoped>
#add {
    color: #000;
    height: 90%;
    background: #fff;
    padding: 30px;
}
.wx-add {
    font-size: 1rem;
    font-weight: bold;
}
.wx-bottom {
    font-weight: normal;
    font-size: 14px;
    margin-top: 20px;
}
.hotle-address {
    font-size: 0.8rem;
    margin: 30px auto;
}
.hotle-name {
    color: #888;
}
</style>



